.header__section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  height: 90px;
  box-shadow: 2px 0 5px #c5c5c5;
  transform: translateX(-50%);
  background-color: #ffffff;
  z-index: 99999;
  transition: height 0.3s ease;
}

.header__section.navHeight {
  height: 65px;
}

.header__section.navHeight .header__navbar {
  height: 65px;
}

.header__section.navHeight .header__navbar .header__logo a {
  padding: 20px 25px 20px 0;
}

.header__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
  transition: 0.3s ease;
}

.header__navbar .header__logo a {
  padding: 25px 25px 25px 0;
}

.header__navbar .header__logo a svg {
  fill: #000000;
}

.header__navbar .nav-links ul {
  display: flex;
  align-items: center;
}

.header__navbar .nav-links ul li a {
  padding: 15px 20px;
  font-family: "Libre Franklin", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  position: relative;
  transition: 0.3s ease;
}

.header__navbar .nav-links ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #000000;
  transition: 0.3s ease;
}

.header__navbar .nav-links ul li a:hover {
  color: #666666;
}

.header__navbar .nav-links ul li a:hover::after {
  width: 100%;
}

.header__navbar .nav-links ul .light-dark {
  margin-left: 25px;
}

.header__navbar .nav-links ul .light-dark .darkThemeBtn {
  cursor: pointer;
  width: 55px;
  height: 23px;
  background-color: #141414;
  border-radius: 15px;
  position: relative;
}

.header__navbar .nav-links ul .light-dark .darkThemeBtn .circle {
  position: absolute;
  top: 49%;
  left: 21%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: 1px solid rgb(51, 51, 51);
  transition: 0.2s ease;
}

.header__navbar .nav-links ul .light-dark .darkThemeBtn.toggleActive .circle {
  left: 79%;
}

.header__navbar .nav-links ul .light-dark .darkThemeBtn .darkTheme_text {
  width: 90px;
  padding-top: 25px;
  font-size: 11px;
  font-family: "Source Serif Pro", serif;
  font-weight: 400;
  letter-spacing: 1px;
  color: #000000;
}

.header__navbar .nav-links ul .light-dark .darkThemeBtn .darkTheme_text::selection {
  background: none;
}

.header__navbar .navBurger .bar {
  width: 30px;
  height: 4px;
  margin: 6px 0;
  background-color: #000000;
  transition: 0.3s ease;
}

.header__navbar .navBurger {
  display: none;
  cursor: pointer;
  padding: 10px;
}

.header__navbar .navBurger.close .bar1 {
  transform: rotate(45deg) translate(6px, 6px);
}

.header__navbar .navBurger.close .bar2 {
  width: 0;
}

.header__navbar .navBurger.close .bar3 {
  transform: rotate(-406deg) translate(8px, -7px);
  transition-delay: 0.1s;
}

.header__navbar .navside {
  position: fixed;
  top: 90px;
  left: 0;
  width: 100%;
  max-width: 100%;
  height: 0;
  background-color: #141414;
  padding: 0;
  transition: 0.3s ease;
  overflow: hidden;
}

.header__navbar .navside.navActive {
  height: 300px;
  padding: 30px 20px;
}

.header__navbar .navside.navHeight {
  top: 65px;
}

.header__navbar .navside ul li a {
  display: block;
  color: #ffffff;
  font-size: 16px;
  font-family: "Libre Franklin", sans-serif;
  font-weight: 400;
  color: #ffffff;
  padding: 10px 0;
  transition: 0.3s ease;
  position: relative;
}

.header__navbar .navside ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #ffffff;
  transition: 0.3s ease;
}

.header__navbar .navside ul li a:hover {
  padding-left: 5px;
}

.header__navbar .navside ul li a:hover::after {
  width: 100%;
}

.header__navbar .navside .light-dark {
  margin-top: 30px;
}

.header__navbar .navside .light-dark .darkThemeBtn {
  cursor: pointer;
  width: 55px;
  height: 23px;
  background-color: #313131;
  border-radius: 15px;
  position: relative;
}

.header__navbar .navside .light-dark .darkThemeBtn .circle {
  position: absolute;
  top: 49%;
  left: 21%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: 1px solid rgb(51, 51, 51);
  transition: 0.2s ease;
}

.header__navbar .navside .light-dark .darkThemeBtn.toggleActive .circle {
  left: 79%;
}

.header__content {
  margin-top: 150px;
  display: flex;
  justify-content: space-between;
  column-gap: 50px;
}

.header__content .header__content_image {
  max-width: 721px;
}

.header__content .header__content_image img {
  width: 100%;
}

.header__content .header__content_text_item {
  max-width: 430px;
}

.header__content .header__content_text_item .header__content_text {
  margin-top: 18px;
  font-family: "Tenor Sans", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 42px;
  text-transform: uppercase;
  color: #000000;
}

.header__content .header__content_text_item .header__content_small_text {
  margin-top: 27px;
  max-width: 419px;
  font-family: "Source Serif Pro", serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #666666;
}

.header__content .header__content_text_item .by {
  margin-top: 28px;
}

.header__photos {
  margin-top: 60px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 74px 20px;
}

.header__photos .header__photo_item {
  max-width: 387px;
}

.header__photos .header__photo_item img {
  width: 100%;
}

.header__photos .light_title {
  margin-top: 24px;
}

.header__photos .info_text {
  margin-top: 18px;
}

.header__photos .by {
  margin-top: 15px;
}

#latest__section {
  padding-top: 90px;
}

#latest__section .latest__container {
  border-top: 1px solid #E6E6E6;
}

#latest__section .latest__container .light_title {
  margin-top: 90px;
  margin-bottom: 30px;
}

#latest__section .latest__container .latest__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 50px 20px;
}

#latest__section .latest__container .latest__items .latest__item {
  max-width: 285px;
}

#latest__section .latest__container .latest__items .latest__item img {
  width: 100%;
}

#latest__section .latest__container .latest__items .latest__item .light_title {
  margin-top: 25px;
}

#latest__section .latest__container .latest__items .latest__item .info_text {
  margin-top: 18px;
}

#latest__section .latest__container .latest__items .latest__item .by {
  margin-top: 15px;
}

#latest__section .latest__container .latest__btn {
  margin-top: 90px;
  text-align: center;
}

#latest__section .latest__container .latest__btn .btn {
  font-size: 12px;
  text-transform: uppercase;
  font-family: "Libre Franklin", sans-serif;
  font-weight: 400;
  padding: 21px 26px;
  letter-spacing: 1px;
  color: #000000;
  border: 1px solid #000000;
  transition: 0.3s ease;
  position: relative;
}

#latest__section .latest__container .latest__btn .btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #999999;
  z-index: -1;
  transition: 0.1s ease;
}

#latest__section .latest__container .latest__btn .btn:hover {
  color: #ffffff;
}

#latest__section .latest__container .latest__btn .btn:hover::after {
  width: 100%;
}

#featured__section {
  padding-top: 90px;
}

#featured__section .featured__container {
  border-top: 1px solid #E6E6E6;
}

#featured__section .featured__container .light_title {
  margin-top: 90px;
}

#featured__section .featured__container .featured__items {
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 50px 20px;
}

#featured__section .featured__container .featured__items .featured_item {
  max-width: 590px;
}

#featured__section .featured__container .featured__items .featured_item img {
  width: 100%;
}

#featured__section .featured__container .featured__items .featured_item .light_title {
  margin-top: 25px;
}

#featured__section .featured__container .featured__items .featured_item .info_text {
  margin-top: 18px;
}

#featured__section .featured__container .featured__items .featured_item .by {
  margin-top: 15px;
}

#toTopBtn {
  position: fixed;
  top: 120%;
  right: 1.2%;
  z-index: 9000;
  transition: 0.3s ease;
}

#toTopBtn.toUpBtnActive {
  top: 88%;
}

#toTopBtn a {
  fill: #000000;
  border: 1px solid #000000;
  padding: 10px;
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease;
}

#toTopBtn a:hover {
  background-color: #999999;
}

#footer__section {
  margin-top: 90px;
  background-color: #f7f7f7;
  padding: 93px 0 95px 0;
}

#footer__section .footer__container .footer__logo {
  display: flex;
  column-gap: 15px;
  padding-bottom: 62px;
  border-bottom: 1px solid #4D4D4D;
}

#footer__section .footer__container .footer__logo svg {
  fill: #000000;
}

#footer__section .footer__container .footer__logo .copyright {
  color: #666666;
  font-size: 12px;
}

#footer__section .footer__container .footer__links {
  margin-top: 60px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

#footer__section .footer__container .footer__links .footer_link ul {
  margin-top: 20px;
}

#footer__section .footer__container .footer__links .footer_link ul li {
  margin-top: 15px;
}

#footer__section .footer__container .footer__links .footer_link ul li a {
  font-family: "Libre Franklin", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}

#footer__section .footer__container .footer__links .footer_link ul li a:hover {
  border-bottom: 1px solid #000000;
}

#footer__section .footer__container .footer__links .footer_link .footer__input {
  margin-top: 20px;
  max-width: 221px;
  position: relative;
}

#footer__section .footer__container .footer__links .footer_link .footer__input input {
  width: 100%;
  height: 48px;
  padding: 18px 80px 18px 20px;
  font-size: 14px;
  border: none;
}

#footer__section .footer__container .footer__links .footer_link .footer__input input::placeholder {
  font-size: 10px;
  font-family: "Libre Franklin", sans-serif;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}

#footer__section .footer__container .footer__links .footer_link .footer__input .footer__btn .btn {
  position: absolute;
  top: 50%;
  left: 83%;
  transform: translate(-50%, -50%);
  padding: 18px 24px;
  font-family: "Libre Franklin", sans-serif;
  font-size: 10px;
  letter-spacing: 1px;
  font-weight: 400;
  background-color: #000000;
  color: #ffffff;
  cursor: pointer;
  border: none;
  transition: 0.2s ease;
}

#footer__section .footer__container .footer__links .footer_link .footer__input .footer__btn .btn:hover {
  background-color: #ffffff;
  color: #000000;
}

#footer__section .footer__container .footer_bottom_links {
  max-width: 600px;
  margin-top: 70px;
  display: flex;
  align-items: flex-start;
}

#footer__section .footer__container .footer_bottom_links .by {
  margin-top: 25px;
  color: #000000;
  font-size: 12px;
}

#footer__section .footer__container .footer_bottom_links .by a {
  color: #000000;
}

#footer__section .footer__container .footer_bottom_links .by a:hover {
  border-bottom: 1px solid #000000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Libre Franklin", sans-serif;
  font-family: "Tenor Sans", sans-serif;
  font-family: "Source Serif Pro", serif;
  list-style: none;
  outline: none;
}

html {
  scroll-behavior: smooth;
}

body.darkMode {
  background-color: #0e0e0e;
  color: #ffffff;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background-color: #666666;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #999999;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
}

.light_title {
  font-size: 10px;
  font-weight: 400;
  font-family: "Libre Franklin", sans-serif;
  color: #999999;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.info_text {
  font-size: 20px;
  font-weight: 400;
  font-family: "Tenor Sans", sans-serif;
  line-height: 24px;
  color: #000000;
  text-transform: uppercase;
}

.by {
  font-weight: 700;
  font-family: "Libre Franklin", sans-serif;
  font-size: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #000000;
}

.by a {
  color: #000000;
}

.by a:hover {
  border-bottom: 1px solid #000000;
}

