@media screen and (min-width: 541px) {
  body .navside {
    display: none;
  }
}

@media screen and (max-width: 1070px) {
  .header__content {
    column-gap: 30px;
  }

  .header__content .header__content_image {
    min-width: 500px;
  }

  .header__content .header__content_text_item .header__content_text {
    font-size: 28px;
    line-height: 37px;
  }

  #latest__section .latest__container .latest__items .latest__item .info_text {
    font-size: 16px;
    font-weight: 700;
  }
}

@media screen and (max-width: 1024px) {
  .header__photos .info_text {
    font-size: 16px;
    font-weight: 700;
  }
}

@media screen and (max-width: 920px) {
  .header__content .header__content_text_item .header__content_text {
    font-size: 26px;
    line-height: 33px;
  }

  #latest__section .latest__container .latest__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 890px) {
  .header__content {
    flex-direction: column-reverse;
    row-gap: 50px;
  }

  .header__content .header__content_image {
    min-width: 150px;
  }

  .header__content .header__content_text_item .header__content_text {
    font-size: 36px;
    line-height: 42px;
  }

  #footer__section .footer__container .footer__links {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 40px;
  }
}

@media screen and (max-width: 740px) {
  .header__photos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  #latest__section .latest__container .latest__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  #latest__section .latest__container .latest__items .latest__item {
    max-width: 100%;
  }

  #featured__section .featured__container .featured__items .featured_item .info_text {
    font-size: 16px;
    font-weight: 700;
  }
}

@media screen and (max-width: 620px) {
  #footer__section .footer__container .footer__links {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 540px) {
  .header__navbar .nav-links {
    display: none;
  }

  .header__navbar .navBurger {
    display: block;
  }

  .header__photos {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .header__photos .header__photo_item {
    max-width: 100%;
  }

  #latest__section .latest__container .latest__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  #featured__section .featured__container .featured__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  #featured__section .featured__container .featured__items .featured_item {
    max-width: 100%;
  }

  #footer__section .footer__container .footer__logo {
    flex-direction: column;
    row-gap: 20px;
  }
}

@media screen and (max-width: 430px) {
  .header__content .header__content_text_item .header__content_text {
    font-size: 26px;
    line-height: 33px;
  }

  #footer__section .footer__container .footer__links {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 350px) {
  .header__content {
    margin-top: 120px;
  }

  .header__content .header__content_text_item .header__content_text {
    font-size: 22px;
    line-height: 27px;
  }
}